import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Button, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import { Callout } from '@Green-Dot-Corporation/eureka-ui-callouts';
import {
    TextInput,
    Textarea,
} from '@Green-Dot-Corporation/eureka-ui-form-elements';
import WindowContainer from '../window-container/WindowContainer';
import GenericMessage from '../generic-message/GenericMessage';
import ModalWrapper from '../modal-wrapper/ModalWrapper';
import PaperAirplane from '../svg/PaperAirplane';
import './styles/leave-message.scss';

import ChatService from '../../service/ChatService';

const { PRIMARY, SECONDARY } = VARIANTS;
class LeaveMessage extends PureComponent {
    static propTypes = {
        contextCls: PropTypes.string,
        accountIdentifier: PropTypes.string,
        email: PropTypes.string,
        leaveAgentMessage: PropTypes.string,
        onChatWidgetClose: PropTypes.func,
        i18n: PropTypes.object,
    };

    state = {
        shouldShowLeaveMessageWindow: true,
        shouldShowMessageSentWindow: false,
        value: '',
        isSendMsgBtnEnabled: false,
        shouldShowErrorMsg: false,
        userEmail: '',
        shouldShowConfirmModal: false,
    };

    render() {
        const {
            shouldShowLeaveMessageWindow,
            shouldShowMessageSentWindow,
            shouldShowConfirmModal,
        } = this.state;

        return (
            <>
                {shouldShowLeaveMessageWindow &&
                    this.renderLeaveMessageWindow()}
                {shouldShowMessageSentWindow && this.renderMessageSentWindow()}
                {shouldShowConfirmModal && this.renderConfirmModal()}
            </>
        );
    }

    componentDidMount() {
        this.container = document.querySelector(
            '.web-chat-window-container__tile',
        );
    }

    renderAirplaneIcon = () => <PaperAirplane />;

    renderLeaveMessageWindow = () => {
        const {
            contextCls,
            email,
            accountIdentifier,
            leaveAgentMessage,
            i18n,
        } = this.props;

        const { value, shouldShowErrorMsg, isSendMsgBtnEnabled } = this.state;

        const shouldShowEmailInput = !(email || accountIdentifier);

        let { userEmail } = this.state;

        if (!shouldShowEmailInput) {
            userEmail = email;
        }

        return (
            <WindowContainer
                contextCls={cx(this.baseCls, contextCls)}
                shouldShowCloseBtn
                onCloseBtnClick={this.handleLeaveMsgCloseBtnClick}
            >
                <div
                    className={cx(
                        `${this.baseCls}__leave-message-content`,
                        contextCls,
                    )}
                >
                    <Callout
                        renderIcon={this.renderAirplaneIcon}
                        title={i18n.getTranslation('leaveMessage.calloutTitle')}
                        msg={leaveAgentMessage}
                    />

                    {shouldShowEmailInput && (
                        // TODO: replace the TextInput when Form field composer UI component available
                        <TextInput
                            contextCls={`${this.baseCls}__input`}
                            placeholder={i18n.getTranslation(
                                'leaveMessage.emailInputText',
                            )}
                            value={userEmail}
                            onChange={this.handleUserEmail}
                            aria-label={i18n.getTranslation(
                                'leaveMessage.emailInputText',
                            )}
                        />
                    )}

                    {/* TODO: replace the Textarea when Form field composer UI component available */}
                    <Textarea
                        rows="4"
                        cols="42"
                        contextCls={`${this.baseCls}__textarea`}
                        placeholder={i18n.getTranslation(
                            'leaveMessage.messageInputText',
                        )}
                        value={value}
                        onChange={this.handleMessageChange}
                        aria-label={i18n.getTranslation(
                            'leaveMessage.messageInputText',
                        )}
                    />

                    {shouldShowErrorMsg && (
                        <p className={`${this.baseCls}__errorMsg`}>
                            {i18n.getTranslation('leaveMessage.errorMsg')}
                        </p>
                    )}

                    <Button
                        contextCls={cx(`${this.baseCls}__sendMsgBtn`)}
                        text={i18n.getTranslation(
                            'leaveMessage.sendMessageBtnText',
                        )}
                        variant={PRIMARY}
                        onClick={this.handleSendMsgBtnClick}
                        aria-label={i18n.getTranslation(
                            'leaveMessage.sendMessageBtnText',
                        )}
                        isDisabled={!isSendMsgBtnEnabled}
                    />
                </div>
            </WindowContainer>
        );
    };

    renderMessageSentWindow = () => {
        const { contextCls, ...restProps } = this.props;

        return (
            <GenericMessage
                contextCls={cx(this.baseCls, contextCls)}
                icon="Success"
                headText="Message sent"
                subText="We will get back to you as soon as we can."
                {...restProps}
            />
        );
    };

    renderConfirmModal = () => {
        const { contextCls } = this.props;
        const modalActions = [
            {
                variant: PRIMARY,
                text: 'Close message',
                ariaLabel: 'Close message',
                onClick: this.handlePrimaryBtnClick,
            },
            {
                variant: SECONDARY,
                text: 'Cancel',
                ariaLabel: 'Cancel',
                onClick: this.handleSecondaryBtnClick,
            },
        ];

        return (
            <ModalWrapper
                contextCls={cx(`${this.baseCls}__modal-wrapper`, contextCls)}
                modalActions={modalActions}
                svgType="info"
                renderTo={() =>
                    document.querySelector('.web-chat-leave-message')
                }
                height={this.container.clientHeight}
                appElement={document.querySelector('.web-chat-leave-message')}
                iconName="EurekaCircleX"
                iconType="INFO"
                title="Are you sure you want to close this message?"
                message="Your message will not be sent. This cannot be undone."
            />
        );
    };

    handlePrimaryBtnClick = () => {
        const { onChatWidgetClose } = this.props;
        this.setState({
            shouldShowLeaveMessageWindow: false,
            shouldShowConfirmModal: false,
        });

        if (onChatWidgetClose) {
            onChatWidgetClose();
        }
    };

    handleSecondaryBtnClick = () => {
        this.setState({ shouldShowConfirmModal: false });
    };

    handleLeaveMsgCloseBtnClick = () => {
        this.setState({ shouldShowConfirmModal: true });
    };

    handleSendMsgBtnClick = async () => {
        const newState = {};

        newState.isSendMsgBtnEnabled = false;

        try {
            const request = {
                accountIdentifier: this.props.accountIdentifier,
                email: this.state.userEmail,
                deviceType: 'Web',
                message: this.state.value,
            };

            const response = await ChatService.leaveMessage(request);

            if (response.isSuccess) {
                newState.shouldShowLeaveMessageWindow = false;
                newState.shouldShowMessageSentWindow = true;
            } else {
                newState.shouldShowErrorMsg = true;
                newState.isSendMsgBtnEnabled = true;
            }
        } catch (error) {
            newState.shouldShowErrorMsg = true;
            newState.isSendMsgBtnEnabled = true;
        } finally {
            this.setState(newState);
        }
    };

    handleMessageChange = (event) => {
        const hasMessage = event.target.value.trim().length > 0;

        const hasUserInfo =
            this.props.email ||
            this.props.accountIdentifier ||
            this.state.userEmail;

        this.setState({
            value: event.target.value,
            isSendMsgBtnEnabled: hasUserInfo && hasMessage,
        });
    };

    handleUserEmail = (event) => {
        const hasEmail = event.target.value.trim().length > 0;

        const hasMessage = this.state.value;

        this.setState({
            userEmail: event.target.value,
            isSendMsgBtnEnabled: hasEmail && hasMessage,
        });
    };

    baseCls = 'web-chat-leave-message';
}

export default LeaveMessage;
